<template>
    <component :is="asyncModalWithOptions" :project="currentProject" />
</template>

<script setup>
import { useRoute } from "vue-router";
import Projects from "@/projects/projects.json";
import ErrorComp from "./404.vue";
import { defineAsyncComponent } from "@vue/runtime-core";

const route = useRoute();
const currentProject = Projects.find((project) => project.title === route.params.project.replace("_", " "));

const asyncModalWithOptions = defineAsyncComponent({
    loader: () => import("../../../src/projects/" + currentProject.title + "/Index.vue"),
    delay: 200,
    timeout: 3000,
    errorComponent: ErrorComp
});
</script>

<style>
.startedOn {
    right: 0;
    bottom: 1rem;
    color: var(--invert-accent-background);
}

.projectTitleContainer {
    padding: 0;
    width: 100%;
    padding-top: 2rem;
    padding-bottom: 1rem;
}
</style>
